<template>
  <Modal title="新建规则" :modelValue="visible" :loading="confirmLoading" @on-cancel="handleCancel">
    <Form :label-width="85" :label-colon="true" ref="form" :model="form" :rules="rule">
      <FormItem prop="desc" label="规则描述">
        <Input v-model="form.desc" type="textarea" :rows="4" placeholder="请输入规则描述" />
      </FormItem>
    </Form>
    <template #footer>
      <Button type="text" @click="handleCancel">取消</Button>
      <Button type="primary" @click="handleSubmit">确定</Button>
    </template>
  </Modal>
</template>

<script>
  export default {
    data() {
      return {
        visible: false,
        confirmLoading: false,
        form: {
          desc: ''
        },
        rule: {
          desc: [{ required: true, message: '请输入规则描述', trigger: 'change' }]
        }
      }
    },
    methods: {
      add() {
        this.form = {}
        this.$refs.form.resetFields()
        this.visible = true
      },
      handleSubmit() {
        this.confirmLoading = true
        this.$refs.form.validate(valid => {
          if (valid) {
            setTimeout(() => {
              this.visible = false
              this.confirmLoading = false
              this.$emit('ok', this.form)
            }, 1500)
          } else {
            this.confirmLoading = false
          }
        })
      },
      handleCancel() {
        this.visible = false
      }
    }
  }
</script>
