<template>
  <Card shadow>
    <div class="table-page-search-wrapper">
      <Form :label-colon="true" ref="form" :model="query">
        <Row :gutter="24">
          <Col :xl="8" :lg="8" :md="12" :sm="24" :xs="24">
            <FormItem label="规则编号">
              <Input v-model="query.id" placeholder="输入规则编号" />
            </FormItem>
          </Col>
          <Col :xl="8" :lg="8" :md="12" :sm="24" :xs="24">
            <FormItem label="使用状态">
              <Select v-model="query.status" placeholder="选择使用状态" clearable>
                <Option :value="0">全部</Option>
                <Option :value="1">关闭</Option>
                <Option :value="2">运行中</Option>
                <Option :value="3">异常</Option>
              </Select>
            </FormItem>
          </Col>
          <template v-if="advanced">
            <Col :xl="8" :lg="8" :md="12" :sm="24" :xs="24">
              <FormItem label="调用次数">
                <Input-number v-model="query.callNo" placeholder="输入调用次数" :min="0" style="width: 100%" />
              </FormItem>
            </Col>
            <Col :xl="8" :lg="8" :md="12" :sm="24" :xs="24">
              <FormItem label="更新日期">
                <DatePicker v-model="query.date" placeholder="选择更新日期" style="width: 100%" />
              </FormItem>
            </Col>
            <Col :xl="8" :lg="8" :md="12" :sm="24" :xs="24">
              <FormItem label="使用状态">
                <Select v-model="query.useStatus" placeholder="选择使用状态" clearable>
                  <Option :value="0">全部</Option>
                  <Option :value="1">关闭</Option>
                  <Option :value="2">运行中</Option>
                  <Option :value="3">异常</Option>
                </Select>
              </FormItem>
            </Col>
            <Col :xl="8" :lg="8" :md="12" :sm="24" :xs="24">
              <FormItem label="使用状态">
                <Select placeholder="选择使用状态" clearable>
                  <Option :value="0">全部</Option>
                  <Option :value="1">关闭</Option>
                  <Option :value="2">运行中</Option>
                  <Option :value="3">异常</Option>
                </Select>
              </FormItem>
            </Col>
          </template>
          <Col :md="(!advanced && 8) || 24" :sm="24">
            <span :style="(advanced && { float: 'right', overflow: 'hidden' }) || {}">
              <Space>
                <Button icon="md-search" type="primary" @click="loadData" :loading="loading">查询</Button>
                <Button icon="md-refresh" @click="resetData">重置</Button>
                <a @click="toggleAdvanced">
                  {{ advanced ? '收起' : '展开' }}
                  <Icon :type="advanced ? 'ios-arrow-up' : 'ios-arrow-down'" />
                </a>
              </Space>
            </span>
          </Col>
        </Row>
      </Form>
    </div>

    <div class="ivu-mb-8">
      <Space>
        <Button type="primary" icon="md-add" @click="$refs.createModal.add()">新建</Button>
        <Button type="dashed" @click="tableOption">{{ (optionAlertShow && '关闭') || '开启' }} alert</Button>
        <Dropdown v-if="selectedRows.length">
          <Button>
            批量操作
            <Icon type="ios-arrow-down" />
          </Button>
          <template #list>
            <DropdownMenu>
              <DropdownItem>
                <Icon type="md-trash" />
                删除
              </DropdownItem>
              <DropdownItem>
                <Icon type="ios-lock" />
                锁定
              </DropdownItem>
            </DropdownMenu>
          </template>
        </Dropdown>
      </Space>
    </div>
    <Alert show-icon v-if="optionAlertShow">
      已选择
      <strong style="color: #2d8cf0">{{ selectedRows.length }}</strong> 项 服务调用次数总计
      <strong>{{ callNoCount }} 次</strong>
      <a class="ivu-ml" @click="() => $refs.table.selectAll(false)">清空</a>
    </Alert>
    <Table ref="table" :columns="columns" :data="tableData.data" :loading="loading" @on-selection-change="onSelectChange">
      <template #status="{ row }">
        <Badge :status="statusMap[`${row.status}`].status" :text="statusMap[`${row.status}`].text" />
      </template>
      <template #description="{ row }">
        <Ellipsis :text="row.description" :length="5" tooltip placement="top" />
      </template>
      <template #action="{ row }">
        <template>
          <a @click="handleEdit(row)">配置</a>
          <Divider type="vertical" />
          <a @click="handleSub(row)">订阅报警</a>
        </template>
      </template>
    </Table>
    <div class="ivu-mt ivu-text-center">
      <Page show-sizer :total="tableData.totalCount" @on-change="changePage" @on-page-size-change="changePageSize" />
    </div>
    <create-form ref="createModal" @ok="loadData" />
    <step-by-step-modal ref="modal" @ok="loadData" />
  </Card>
</template>

<script>
  import CreateForm from './modules/CreateForm'
  import StepByStepModal from './modules/StepByStepModal'
  import { getServiceList } from '@/api/manage'

  export default {
    name: 'TableList',
    components: {
      CreateForm,
      StepByStepModal
    },
    data() {
      return {
        statusMap: {
          0: {
            status: 'default',
            text: '关闭'
          },
          1: {
            status: 'processing',
            text: '运行中'
          },
          2: {
            status: 'success',
            text: '已上线'
          },
          3: {
            status: 'error',
            text: '异常'
          }
        },
        advanced: false,
        query: {
          pageNo: 1,
          pageSize: 10,
          callNo: null
        },
        columns: [
          {
            type: 'selection',
            align: 'center',
            width: 60
          },
          {
            title: '序号',
            align: 'center',
            width: 80,
            render: (h, params) => h('span', params.index + (this.query.pageNo - 1) * this.query.pageSize + 1)
          },
          {
            title: '规则编号',
            key: 'no',
            minWidth: 100
          },
          {
            title: '描述',
            key: 'description',
            slot: 'description',
            minWidth: 150
          },
          {
            title: '服务调用次数',
            key: 'callNo',
            sortable: true,
            align: 'right',
            minWidth: 150,
            render: (h, params) => h('span', `${params.row.callNo} 次`)
          },
          {
            title: '状态',
            key: 'status',
            slot: 'status',
            minWidth: 100
          },
          {
            title: '更新时间',
            key: 'updatedAt',
            sortable: true,
            minWidth: 180
          },
          {
            title: '操作',
            key: 'action',
            slot: 'action',
            align: 'center',
            width: 150
          }
        ],
        tableData: [],
        loading: false,
        selectedRows: [],
        optionAlertShow: false
      }
    },
    created() {
      this.loadData()
      this.tableOption()
    },
    computed: {
      callNoCount() {
        if (this.selectedRows.length) {
          return this.selectedRows.map(item => item.callNo).reduce((v1, v2) => v1 + v2, 0)
        }
        return 0
      }
    },
    methods: {
      loadData() {
        this.loading = true
        const req = { ...this.query }
        getServiceList(req).then(res => {
          this.tableData = res
          this.loading = false
        })
      },
      resetData() {
        this.query = {
          pageNo: 1,
          pageSize: 10,
          callNo: null
        }
        this.loadData()
      },
      changePage(pageNo) {
        this.query.pageNo = pageNo
        this.loadData()
      },
      changePageSize(pageSize) {
        this.query.pageSize = pageSize
        this.loadData()
      },
      tableOption() {
        this.optionAlertShow = !this.optionAlertShow
      },
      handleEdit(record) {
        this.$refs.modal.edit(record)
      },
      handleSub(record) {
        if (record.status !== 0) {
          this.$Message.info(`${record.no} 订阅成功`)
        } else {
          this.$Message.error(`${record.no} 订阅失败，规则已关闭`)
        }
      },
      onSelectChange(selection) {
        this.selectedRows = selection
      },
      toggleAdvanced() {
        this.advanced = !this.advanced
      }
    }
  }
</script>
