<template>
  <div>
    <Card shadow>
      <Row class="ivu-text-center">
        <Col :sm="8" :xs="24">
          <p>我的待办</p>
          <p v-font="24">8个任务</p>
        </Col>
        <Col :sm="8" :xs="24">
          <p>本周任务平均处理时间</p>
          <p v-font="24">32分钟</p>
        </Col>
        <Col :sm="8" :xs="24">
          <p>本周完成任务数</p>
          <p v-font="24">24个</p>
        </Col>
      </Row>
    </Card>

    <Card class="ivu-mt" shadow title="标准列表">
      <template #extra>
        <RadioGroup v-model="status" type="button">
          <Radio label="all">全部</Radio>
          <Radio label="processing">进行中</Radio>
          <Radio label="waiting">等待中</Radio>
        </RadioGroup>
        <Input placeholder="请输入..." suffix="ios-search" style="margin-left: 16px; width: 200px" />
      </template>
      <div class="operate">
        <Button type="dashed" long icon="md-add" @click="$refs.taskForm.value = true">添加</Button>
      </div>

      <List size="large">
        <ListItem :key="index" v-for="(item, index) in data">
          <ListItemMeta :description="item.description">
            <template #avatar>
              <Avatar size="large" shape="square" :src="item.avatar" />
            </template>
            <template #title>
              <a>{{ item.title }}</a>
            </template>
          </ListItemMeta>
          <template #action>
            <li>
              <Button type="text" size="small" @click="edit(item)">编辑</Button>
            </li>
            <li>
              <Dropdown>
                <Button type="text" size="small"
                  >更多
                  <Icon type="ios-arrow-down" />
                </Button>
                <template #list>
                  <DropdownMenu>
                    <DropdownItem @click="edit(item)">编辑</DropdownItem>
                    <DropdownItem @click="remove(item)">删除</DropdownItem>
                  </DropdownMenu>
                </template>
              </Dropdown>
            </li>
          </template>
          <div class="list-content">
            <div class="list-content-item">
              <span>负责人</span>
              <p>{{ item.owner }}</p>
            </div>
            <div class="list-content-item">
              <span>开始时间</span>
              <p>{{ item.startAt }}</p>
            </div>
            <div class="list-content-item">
              <Progress
                :stroke-width="6"
                :percent="item.progress.value"
                :status="item.progress.status ? item.progress.status : null"
                style="width: 180px"
              />
            </div>
          </div>
        </ListItem>
      </List>
    </Card>
    <task-form ref="taskForm" />
  </div>
</template>

<script>
  import TaskForm from './modules/TaskForm'

  const data = []
  data.push({
    title: 'iView',
    avatar: 'https://dev-file.iviewui.com/p50TGdvvpXWVR06Vu2TAwkpRnpt8FURA/avatar',
    description: '一套基于 Vue.js 的企业级 UI 组件库',
    owner: 'Aresn',
    startAt: '2022-12-01 17:00:00',
    progress: {
      value: 90,
      status: 'active'
    }
  })
  data.push({
    title: 'iView Pro',
    avatar: 'https://dev-file.iviewui.com/WLXm7gp1EbLDtvVQgkeQeyq5OtDm00Jd/avatar',
    description: '基于 iView 的高阶组件库',
    owner: '中小鱼',
    startAt: '2022-12-01 17:00:00',
    progress: {
      value: 100,
      status: 'success'
    }
  })
  data.push({
    title: 'iView Admin Pro',
    avatar: 'https://dev-file.iviewui.com/fAenQ8nvRjL7x0i0jEfuDBZHvJfHf3v6/avatar',
    description: '基于 iView 的企业级中后台系统',
    owner: 'Echo',
    startAt: '2019-02-01 14:00:00',
    progress: {
      value: 75
    }
  })
  data.push({
    title: 'iView Developer',
    avatar: 'https://dev-file.iviewui.com/ttkIjNPlVDuv4lUTvRX8GIlM2QqSe0jg/avatar',
    description: 'iView 官方社区',
    owner: '唐不苦',
    startAt: '2019-02-01 14:00:00',
    progress: {
      value: 75
    }
  })
  data.push({
    title: 'iView Run',
    avatar: 'https://dev-file.iviewui.com/4Z0QR2L0J1XStxBh99jVJ8qLfsGsOgjU/avatar',
    description: 'iView 在线运行编辑器',
    owner: '甜筒',
    startAt: '2022-12-01 17:00:00',
    progress: {
      value: 75,
      status: 'wrong'
    }
  })

  export default {
    name: 'StandardList',
    components: { TaskForm },
    data() {
      return {
        data,
        status: 'all'
      }
    },
    methods: {
      edit(record) {
        this.$refs.taskForm.value = true
        this.$refs.taskForm.form = { ...record }
      },
      remove(record) {
        this.data = this.data.filter(item => item.title !== record.title)
      }
    }
  }
</script>

<style lang="less" scoped>
  .list-content {
    font-size: 0;

    .list-content-item {
      display: inline-block;
      margin-left: 40px;
      color: #808695;
      font-size: 14px;
      vertical-align: middle;

      p {
        margin-top: 4px;
        line-height: 22px;
      }
    }
  }

  :deep(.ivu-list-item-action) {
    .ivu-dropdown-menu {
      min-width: auto;
    }
  }
</style>
