<template>
  <Modal title="分步对话框" :modelValue="visible" :loading="confirmLoading" @on-cancel="handleCancel">
    <Steps :current="currentStep" :style="{ marginBottom: '24px' }">
      <Step title="基本信息" />
      <Step title="配置规则属性" />
      <Step title="设定调度周期" />
    </Steps>
    <Form :label-width="85" :label-colon="true" ref="form" :model="form" :rules="rule">
      <div v-show="currentStep === 0">
        <FormItem prop="name" label="规则名称">
          <Input v-model="form.name" placeholder="请输入规则名称" />
        </FormItem>
        <FormItem prop="desc" label="规则描述">
          <Input v-model="form.desc" type="textarea" :rows="4" placeholder="请输入规则描述" />
        </FormItem>
      </div>
      <div v-show="currentStep === 1">
        <FormItem prop="target" label="监控对象">
          <Select v-model="form.target" placeholder="请选择监控对象" style="width: 100%">
            <Option value="1">表一</Option>
            <Option value="2">表二</Option>
          </Select>
        </FormItem>
        <FormItem prop="template" label="规则模板">
          <Select v-model="form.template" placeholder="请选择规则模板" style="width: 100%">
            <Option value="1">规则模板一</Option>
            <Option value="2">规则模板二</Option>
          </Select>
        </FormItem>
        <FormItem prop="type" label="规则类型">
          <RadioGroup v-model="form.type" style="width: 100%">
            <Radio label="1" border>强</Radio>
            <Radio label="2" border>弱</Radio>
          </RadioGroup>
        </FormItem>
      </div>
      <div v-show="currentStep === 2">
        <FormItem prop="time" label="开始日期">
          <DatePicker v-model="form.time" placeholder="请选择开始日期" style="width: 100%" />
        </FormItem>
        <FormItem prop="frequency" label="调度周期">
          <Select v-model="form.frequency" placeholder="请选择调度周期" style="width: 100%">
            <Option value="month">月</Option>
            <Option value="week">周</Option>
          </Select>
        </FormItem>
      </div>
    </Form>
    <template #footer>
      <Button @click="currentStep--" v-if="currentStep > 0" :style="{ float: 'left' }">上一步</Button>
      <Button @click="handleCancel">取消</Button>
      <Button @click="handleNext(currentStep)" :loading="confirmLoading" type="primary">{{
        (currentStep === 2 && '完成') || '下一步'
      }}</Button>
    </template>
  </Modal>
</template>

<script>
  const stepForms = [
    ['name', 'desc'],
    ['target', 'template', 'type'],
    ['time', 'frequency']
  ]

  export default {
    name: 'StepByStepModal',
    data() {
      return {
        visible: false,
        confirmLoading: false,
        currentStep: 0,
        form: {
          name: '',
          desc: '',
          target: '',
          template: '',
          type: '',
          time: '',
          frequency: ''
        },
        rule: {
          name: [{ required: true, message: '请输入规则名称', trigger: 'blur' }],
          desc: [{ required: true, message: '请输入规则描述', trigger: 'blur' }],
          target: [{ required: true, message: '请选择监控对象', trigger: 'change' }],
          template: [{ required: true, message: '请选择规则模板', trigger: 'change' }],
          type: [{ required: true, message: '请选择规则类型', trigger: 'change' }],
          time: [
            {
              required: true,
              message: '请选择开始日期',
              trigger: 'change',
              pattern: /.+/
            }
          ],
          frequency: [{ required: true, message: '请选择调度周期', trigger: 'change' }]
        }
      }
    },
    methods: {
      edit(record) {
        this.visible = true
        this.$nextTick(() => {
          this.form = { ...record }
        })
      },
      handleNext(step) {
        const currentStep = step + 1
        if (currentStep <= 2) {
          const currentStepForm = stepForms[this.currentStep]
          let valid = true
          currentStepForm.forEach(item =>
            this.$refs.form.validateField(item, error => {
              if (error) valid = false
            })
          )
          if (valid) this.currentStep = currentStep
          return
        }
        this.confirmLoading = true
        this.$refs.form.validate(valid => {
          if (valid) {
            setTimeout(() => {
              this.visible = false
              this.confirmLoading = false
              this.$emit('ok', this.form)
            }, 1500)
          } else {
            this.confirmLoading = false
          }
        })
      },
      handleCancel() {
        this.visible = false
        this.currentStep = 0
        this.form = {}
        this.$refs.form.resetFields()
      }
    }
  }
</script>
