<template>
  <Modal :modelValue="value" title="任务编辑" @on-ok="onOk" @on-cancel="onCancel">
    <Form :label-colon="true" label-position="top" ref="form" :model="form" :rules="rule">
      <FormItem prop="title" label="任务名称">
        <Input v-model="form.title" placeholder="请输入任务名称" />
      </FormItem>
      <FormItem prop="startAt" label="开始时间">
        <DatePicker v-model="form.startAt" style="width: 100%" type="datetime" placeholder="请选择开始时间" />
      </FormItem>
      <FormItem prop="owner" label="负责人">
        <Select v-model="form.owner" placeholder="请选择负责人">
          <Option value="中小鱼">中小鱼</Option>
          <Option value="唐不苦">唐不苦</Option>
          <Option value="甜筒">甜筒</Option>
          <Option value="Echo">Echo</Option>
          <Option value="Aresn">Aresn</Option>
        </Select>
      </FormItem>
      <FormItem label="任务描述">
        <Input type="textarea" v-model="form.description" placeholder="请输入任务描述" />
      </FormItem>
    </Form>
  </Modal>
</template>

<script>
  export default {
    name: 'TaskForm',
    props: {
      record: {
        type: Object,
        default: () => {}
      }
    },
    data() {
      return {
        value: false,
        form: {
          title: '',
          startAt: '',
          owner: '',
          description: ''
        },
        rule: {
          title: [{ required: true, message: '请输入任务名称', trigger: 'blur' }],
          startAt: [
            {
              required: true,
              message: '请选择开始时间',
              trigger: 'change',
              pattern: /.+/
            }
          ],
          owner: [{ required: true, message: '请选择负责人', trigger: 'change' }]
        }
      }
    },
    mounted() {},
    methods: {
      onOk() {
        this.$refs.form.validate(valid => {
          if (valid) {
            this.$Message.success('Success!')
          } else {
            this.$Message.error('Fail!')
          }
          this.onCancel()
        })
      },
      onCancel() {
        this.value = !this.value
        this.form = {}
        this.$refs.form.resetFields()
      }
    }
  }
</script>
