<template>
  <PageView :content="content">
    <Card shadow>
      <List item-layout="vertical">
        <ListItem v-for="(item, index) in data" :key="index">
          <ListItemMeta>
            <template #avatar>
              <RouterLink :to="item.userLink" target="_blank">
                <Avatar :src="item.avatar" />
              </RouterLink>
            </template>
            <template #title>
              <RouterLink :to="item.userLink" target="_blank">{{ item.name }}</RouterLink>
            </template>
          </ListItemMeta>
          <div>
            <p>
              {{ item.id }}
              <Button size="small" icon="md-copy" @click="handleCopyId(item.id)">复制</Button>
            </p>
            <p>邮箱地址：{{ item.mail }}</p>
          </div>
          <div class="ivu-mt-8">
            <Tag :color="item.is_check ? 'green' : 'red'">{{ item.is_check ? '已验证邮箱' : '未验证邮箱' }}</Tag>
            <Tag :color="item.status === 1 ? 'blue' : item.status === 2 ? 'purple' : 'volcano'">
              {{ item.status === 1 ? '普通用户' : item.status === 2 ? '管理员' : '运营人员' }}
            </Tag>
            <Button class="ivu-ml" size="small" icon="md-list" @click="handleCollapse(index)">
              {{ item.is_collapse ? '收起' : '展开' }}操作
            </Button>
          </div>
          <template #action v-if="item.is_collapse">
            <li>
              <Poptip confirm transfer :disabled="item.status === 1" title="确认将该用户设置为普通用户吗？" @on-ok="handleSetStatus(index, 1)">
                <Button size="small" type="primary" :disabled="item.status === 1">普通用户</Button>
              </Poptip>
            </li>
            <li>
              <Poptip confirm transfer :disabled="item.status === 2" title="确认将该用户设置为管理员吗？" @on-ok="handleSetStatus(index, 2)">
                <Button size="small" type="primary" :disabled="item.status === 2">管理员</Button>
              </Poptip>
            </li>
            <li>
              <Poptip confirm transfer :disabled="item.status === 3" title="确认将该用户设置为运营人员吗？" @on-ok="handleSetStatus(index, 3)">
                <Button size="small" type="primary" :disabled="item.status === 3">运营人员</Button>
              </Poptip>
            </li>
          </template>
        </ListItem>

        <template #footer>
          <div class="ivu-mt ivu-text-center">
            <Page :total="100" show-elevator show-sizer show-total />
          </div>
        </template>
      </List>
    </Card>
  </PageView>
</template>

<script>
  import { PageView } from '@/components'

  export default {
    name: 'UserList',
    components: { PageView },
    data() {
      return {
        content: '包含用户信息的列表，带有常规操作。',
        data: [
          {
            id: '123456700',
            name: '管理员',
            avatar: 'https://dev-file.iviewui.com/avatar_default/avatar',
            userLink: 'https://dev.iviewui.com/user/1016955559255085056',
            mail: 'user@iview.design',
            status: 2,
            is_check: 1,
            is_collapse: 0
          },
          {
            id: '123456701',
            name: '中小鱼',
            avatar: 'https://dev-file.iviewui.com/userinfoYLhfo1S945BOLuFT96NRStYeYDFRviF5/avatar',
            userLink: 'https://dev.iviewui.com/user/1023876335417823232',
            mail: 'user@iview.design',
            status: 1,
            is_check: 0,
            is_collapse: 0
          },
          {
            id: '123456702',
            name: 'Echo',
            avatar: 'https://dev-file.iviewui.com/userinfoxlXwHVwZkCQtl1Zyd1wrvF78b1rZkhfK/avatar',
            userLink: 'https://dev.iviewui.com/user/1023207710763651072',
            mail: 'user@iview.design',
            status: 3,
            is_check: 1,
            is_collapse: 0
          },
          {
            id: '123456703',
            name: '唐不苦',
            avatar: 'https://dev-file.iviewui.com/userinfosvaY5tb7yfnSFTTimcjy3vuSG6RC28v2/avatar',
            userLink: 'https://dev.iviewui.com/user/1024122983062573056',
            mail: 'user@iview.design',
            status: 1,
