            is_collapse: 0
          }
        ]
      }
    },
    methods: {
      handleCopyId(text) {
        this.$Copy({ text })
      },
      handleCollapse(index) {
        this.data[index].is_collapse = !this.data[index].is_collapse
      },
      handleSetStatus(t, e) {
        this.data[t].status = e
        this.$Message.success('设置成功')
      }
    }
  }
</script>
